html {
  height: 100%;
}

body {
  height: 100%;
  background-color: #eaeaea;
  overflow: hidden;
}

.ui.fixed.menu {
  background-image: linear-gradient(
    135deg,
    rgb(8, 10, 87) 0%,
    rgb(62, 130, 153) 83%,
    rgb(32, 167, 172) 95%
  );
}

.ui.main.container {
  margin-top: 7em;
}

.ui.wide.container {
  width: 100%;
  /* height: 100%; */
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 53px;
  overflow: hidden;
}

.masthead {
  display: flex;
  align-items: center;
  background-image: linear-gradient(
    135deg,
    rgb(8, 10, 87) 0%,
    rgb(62, 130, 153) 83%,
    rgb(32, 167, 172) 95%
  ) !important;
  height: 100vh;
}

.masthead h1.ui.header {
  font-size: 4em;
}

/** 読み取り専用列 */
.jexcel > tbody > tr > td.readonly {
  color: rgba(0, 0, 0, 1) !important;
  background-color: bisque;
}

.ui.menu.jmenu {
  margin-bottom: 0;
}

.pusher.jpusher {
  overflow: hidden;
}

